<template>
    <div class="way3">
     <mt-header fixed title="账号密码登录">
		  <router-link to="/login" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
	  </mt-header>
	  <div class="loginInfo">
		<el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
		  <el-form-item label="账号" prop="userID">
		    <el-input v-model="ruleForm.userID" placeholder="请输入您的账号"></el-input>
		  </el-form-item>
		  <el-form-item label="密码" prop="password">
		    <el-input v-model="ruleForm.password" placeholder="请输入密码"></el-input>
		  </el-form-item>
		  <el-button>登录</el-button>
		</el-form>
	  </div>
    </div>
</template>

<script>
	export default {
    data() {
      return {
        ruleForm: {
          userID: '',
          password: ''
        },
        rules: {
          userID: [
            { required: true, message: '账号不能为空！！！', trigger: 'blur' }
          ],
          password: [
            { required: true, message: '密码不能为空！！！', trigger: 'blur' }
          ]
        }
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }
    }
  }
</script>

<style scoped>
	.way3 li{
		list-style: none;
		display: inline-block;
	}
	.way3 a{
		text-decoration: none;
	}
	.way3 .mint-header{
		height: 50px;
		background-color: #42d885;
	}
	.way3 .loginInfo{
		margin-top: 30%;
	}
	.way3 .loginInfo .mint-button{
		margin:10% 0;
	}
	.way3 .el-form-item__label{
		text-align: inherit;
		width: 20% !important; 
	}
	.way3 .el-form-item__content{
		margin-left: 20% !important;
	}
	.way3 .el-button{
		width: 100%;
		background: #8edcb5;
		color: #fff;
		border: 1px solid #8edcb5;
	}
</style>



