<template>
    <div class="login">
     <mt-header fixed title="登录">
		  <router-link to="/myfooter/mine" slot="left">
		    <mt-button icon="back"></mt-button>
		  </router-link>
		  <router-link to="/register" slot="right">
		    <mt-button>注册</mt-button>
		  </router-link>
	  </mt-header>
	  <div class="loginway">
	  	<span>推荐登录方式</span>
	  	<router-link to="/weixin">
	  		<mt-button style="background:#00cc00;color:#fff;"size="large" icon="weixin">
	  		<img slot="icon" src="../../assets/mineImg/weixin.png">
	  		<span>微信登录</span>
	  	</mt-button>
	  	</router-link>
	  	<span>或</span>
	  	<router-link to="/phone">
	  		<mt-button plain size="large">使用手机号登录</mt-button>
	  	</router-link>
	  	 <mt-button type="default" @click="otherway">使用其他方式登录</mt-button>
		 <mt-popup class="otherWays" v-model="popupVisible" position="bottom">
		 	<li>
		 		<router-link to="/way3">
		 			<div>
		 				<img src="../../assets/mineImg/weibo.png">
		 				<div>微博</div>
		 			</div>
		 		</router-link>
		 	</li>
		 	<li>
		 		<router-link to="/way3">
		 			<div>
		 				<img src="../../assets/mineImg/qq.png">
		 				<div>qq</div>
		 			</div>
		 		</router-link>
		 	</li>
		 	<li>
		 		<router-link to="/way3">
		 			<div>
		 				<img src="../../assets/mineImg/suoding.png">
		 				<div>账号密码</div>
		 			</div>
		 		</router-link>
		 	</li>
		 	<div class="cancel" @click="popupVisible=false">取消</div>
		 </mt-popup>
	  	
	  </div>
    </div>
</template>

<script>
    export default {
    data(){
    	return{
    	popupVisible: false,
    	}
    },
    methods: {
    	otherway(){
    	this.popupVisible=true;
    	}
    }
    }
</script>

<style scoped>
	.login li{
		list-style: none;
		display: inline-block;
	}
	.login a{
		text-decoration: none;
	}
	.login .mint-header{
		height: 50px;
		background-color: #42d885;
	}
	.login .loginway{
		margin-top: 35%;
		text-align: center;
		/*background: #0ff;*/
	}
	.login .loginway .mint-button{
		margin:10% 0;
	}
	/*更多方式*/
	.otherWays{
		height:150px;
		width: 100%; 
	}
	.otherWays li{
		width: 30%;
		padding: 5% 1%;
	}
	.otherWays .cancel{
	border-top: 1px solid #dad5d5;
    line-height: 42px;
	}
</style>



